.comment-item {
  border: 3px solid #f7f7f7;
  border-radius: 10px;
  box-sizing: border-box;
  color: var(--Sub-texto, #5e6282);
  font-family: 'Inter', sans-serif;
  font-size: 12px;
  margin: 0;
  padding: 24px 32px;
  position: relative;
  width: 250px;
}

.comment-item--dark.comment-item {
  border-color: var(--gray-200);
}

.comment-item--first {
  background: var(--Blanco, #fff);
  border: 3px solid white;  
  box-shadow: 0px 4px 80px 0px rgba(0, 0, 0, 0.05);
}

.comment-item--dark.comment-item--first {
  border-color: #08033D;
  background-color: #08033D;
  color: white;
}

.comment-item__image {
  background-color: #fff;
  border-radius: 50%;
  height: 68px;
  left: 50%;
  position: absolute;
  top: 0;
  transform: translateX(-50%) translateY(-75%);
  width: 68px;
}

.comment-item__caption {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.comment-item__text {
  font-weight: 300;
  line-height: 30px; /* 187.5% */
  margin: 0;
  margin-bottom: 20px;
}

.comment-item__author {
  font-size: 18px;
  margin: 0;
}

.comment-item--dark .comment-item__author {
  color: white;
}

.comment-item__destination {
  font-size: 12px;
  margin: 0;  
}

.comment-item--dark .comment-item__destination {
  color: white;
}

.comment-item__icon {
  margin: 0 5px;
  position: relative;
  top: 6px;
}

.comment-item--dark .comment-item__plane-path {
  fill: white
}

@media screen and (min-width: 720px) {
  .comment-item {    
    font-size: 16px;
    height: 232px;
    width: 500px;
  }

  .comment-item__image {
    left: 0;
    transform: translateX(-50%) translateY(-50%);
  }
}

